<template>
    <div class="Withdraw">
        <MyMenu />
        <a-row>
            <a-col :span="this.g.container.span" :offset="this.g.container.offset">

                <div v-if="!(authInfo && authInfo.status == 3)">
                    <router-link to="/my/authentication">
                        <h4><a href="#/my/authentication">请先验证身份后再来申请提现！</a></h4>
                    </router-link>
                </div>

                <div v-if="authInfo && authInfo.status == 3">

                    <a-row class="mtb30 t-left" type="flex">
                        <a-col flex="200px"> Select Withdraw Type：</a-col>
                        <a-col flex="auto">
                            <a-radio-group v-model="withdrawChargesDefault" @change="onRadioChange">
                                <a-radio-button v-for="item in withdrawChargesList" :key="item.id" :value="item.label">
                                    {{item.label.toLocaleUpperCase()}}
                                </a-radio-button>
                            </a-radio-group>
                        </a-col>
                    </a-row>
                    <a-row class="mtb30 t-left" type="flex" align="middle">
                        <a-col flex="200px">Withdraw Amount：</a-col>
                        <a-col flex="auto">
                            <a-input placeholder="Withdraw Amount" />
                        </a-col>
                    </a-row>

                    <a-row class="mtb30 t-left" type="flex" align="middle">
                        <a-col flex="200px">Charges：</a-col>
                        <a-col flex="auto">
                            {{withdrawCharges}}
                        </a-col>
                    </a-row>
                    <a-row class="mtb30 t-left" type="flex" align="middle">
                        <a-col flex="200px">Withdraw Amount：</a-col>
                        <a-col flex="auto">
                            0 BTC
                        </a-col>
                    </a-row>
                    <a-row class="mtb30 t-left" type="flex" align="middle">
                        <a-col flex="auto">
                            <a-button type="primary" block>
                                提现申请
                            </a-button>
                        </a-col>
                    </a-row>

                    <hr class="mtb80">
                    <a-row>
                        <a-col class="t-left">
                            <h5>Apply Record</h5>
                        </a-col>
                    </a-row>

                    <a-table :columns="columns" :dataSource="tableDatas" :loading="loading" :pagination="pagination" @change="handleTableChange" :rowKey="tableDatas => tableDatas.id">
                        <template slot="status" slot-scope="text, record">
                            <div v-if="record.status == 2">
                                <!-- <a-popconfirm :title="record.failed_reason" @confirm="() => onDelete(record.key)">
                                    <a href="javascript:;">view</a>
                                </a-popconfirm> -->
                                <a-tooltip placement="topLeft">
                                    <template slot="title">
                                        <span>{{record.failed_reason}}</span>
                                    </template>
                                    <a-button>{{text}}</a-button>
                                </a-tooltip>
                            </div>
                            <div v-if="record.status != 2">
                                {{text}}
                            </div>

                        </template>

                    </a-table>
                </div>

            </a-col>

        </a-row>

    </div>
</template>


<script>
import MyMenu from "@/components/MyMenu.vue";
export default {
    name: "Withdraw",
    components: {
        MyMenu,
    },
    data() {
        return {
            loading: false,
            authInfo: null,
            withdrawChargesList: [],
            withdrawChargesDefault: 'btc',
            withdrawCharges: 0,


            tableDatas: [],
            pagination: {
                total: 0,
                pageSize: 5,
            },
            queryParam: {
                page: 1,
                size: 5,
                user_id: this.g.userInfo.id,
            },
            columns: [
                {
                    title: "Coin Type",
                    dataIndex: "type",
                    width: "10%",
                }, {
                    title: "Apply Amount",
                    dataIndex: "apply_amount",
                    width: "10%",
                }, {
                    title: "Real Amount",
                    dataIndex: "real_amount",
                    width: "10%",
                }, {
                    title: "Charges",
                    dataIndex: "charges",
                    width: "10%",
                }, {
                    title: "Address",
                    dataIndex: "address",
                }, {
                    title: "Status",
                    dataIndex: "statusText",
                    scopedSlots: { customRender: 'status' },
                    width: "20%",
                }, {
                    title: "Apply Time",
                    dataIndex: "create_datetime",
                    width: "20%",
                },
            ],

        };
    },
    methods: {
        onRadioChange(e) {
            this.withdrawCharges = getCharges(this.withdrawChargesList, e.target.value).value
        },
        handleTableChange(pagination) {
            this.pagination.current = pagination.current;
            this.pagination.pageSize = pagination.pageSize;
            this.queryParam.page = pagination.current;
            this.queryParam.size = pagination.pageSize;
            this.getTableList();
        },
        formatDate(list) {
            // let _t=this
            let a = list;
            a.forEach(function (item) {
                item.create_datetime = moment(item.create_datetime).format("YYYY-MM-DD HH:mm:ss");
                if (item.status == 1) {
                    item.statusText = "Applying "
                } else if (item.status == 2) {
                    item.statusText = "Reject"
                } else if (item.status == 3) {
                    item.statusText = "Pass"
                } else if (item.status == 4) {
                    item.statusText = "Successful withdrawal"
                }
            });
            return a;
        },
        getTableList() {
            $.get(`${this.g.apiUrl}/api/my/withdraw`, this.queryParam, (res) => {
                if (res.flag != "ok") {
                    //提示错误
                    this.$message.error(res.flag);
                    return;
                }

                const pagination = { ...this.pagination };
                pagination.total = res.data.page.total;
                this.tableDatas = this.formatDate(res.data.list);
                this.pagination = pagination;

                this.authInfo = res.data.authInfo
                this.withdrawChargesList = res.data.withdrawCharges
                this.withdrawCharges = getCharges(this.withdrawChargesList, this.withdrawChargesDefault).value
            })
        },
        init() {
            this.getTableList()
        },
    },
    mounted() {
        this.init();
    },
};

function getCharges(arr, key) {
    for (let i = 0; i < arr.length; i++) {
        let item = arr[i]
        if (item.label == key) {
            return item
        }
    }
}
</script>



